* {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

.wrapper {
  display: flex;
  margin: 0 auto;
  flex-wrap: wrap;
  height: calc(100vh - 50px);
  align-items: center;
}

.page-header {
  height: 50px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.24), 0px 0px 4px rgba(0, 0, 0, 0.12);
  flex-basis: 100%;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.menu {
  display: flex;
  align-items: center;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 35px;
  height: 35px;
  border-radius: 50%;
}

.project-name {
  margin: 0 0 0 10px;
  font-weight: bold;
}

.toolbars {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin-left: 10px;
  height: 100%;
}

.card-text {
  opacity: 0.55;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
}

#tools .fas {
  margin-right: 20px;
}

#tools .far {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  font-size: 20px;
  margin-right: 20px;
}


#current-color, .prevColor, .defaultBlue, .defaultRed {
  border: 1px solid #000;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-right: 20px;
  cursor: pointer;
}

#current-color::-webkit-color-swatch-wrapper {
  padding: 0;    
}

#current-color::-webkit-color-swatch {
  border: none;  
  border-radius: 50%;
}

.card-text label {
  margin: 0;
  cursor: pointer;
}

#tools > p {
  cursor: pointer;
}

#canvas {
  border: 1px solid #000;
  align-self: center;
  margin: 20px 0;
  width: 512px;
  height: 512px;
  image-rendering: pixelated;
}

.active-colors {
  opacity: 1;
  cursor: pointer;
}

.active-tool {
  opacity: 1;
  background-color: #f4f0f0;
}

.canvas-section {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 516px;
  justify-content: center;
  margin-left: 20%;
}

.canvas-section .toolbar {
  display: flex;
  justify-content: space-between;
}

#load-button, #grayscale-button {
  border-radius: 8%;
  width: fit-content;
  height: fit-content;
}

#city-input {
  margin: 0 10px;
}

.btn-group > .btn {
  width: fit-content;
  height: fit-content;
  border-radius: .25rem;
}

.active-button {
  background-color: #fff;
  color: #6c757d;
}